.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f44336;
  color: white;
  padding: 10px 20px;
}

.header .logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.header nav a {
  margin: 0 10px;
  color: white;
  text-decoration: none;
}

.header nav a:hover {
  text-decoration: underline;
}
