.about-us-page {
  font-family: 'Arial', sans-serif;
  direction: rtl; /* Right-to-left alignment for Hebrew */
  text-align: right;
  padding: 20px;
  margin: 20px auto;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  max-width: 800px;
}

.about-us-title {
  text-align: center;
  color: #f44336;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.about-us-text {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #333;
}

.contact-email,
.contact-phone {
  color: #f44336;
  text-decoration: none;
  font-weight: bold;
}

.contact-email:hover,
.contact-phone:hover {
  text-decoration: underline;
}
