/* Set the entire app to use flexbox */
body, html, #root {
  height: 100%; /* Ensure the full height is utilized */
  margin: 0;
  display: flex;
  flex-direction: column;
}

/* Ensure the content stretches to fill the space */
.app-container {
  display: flex;
  flex-direction: column;
  flex: 1; /* Push the footer to the bottom */
}

footer {
  background-color: #222;
  color: white;
  text-align: center;
  padding: 10px 20px;
  width: 100%;
}
